.body{
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  box-sizing: border-box;
}

.main{
  flex: 1 1;
  margin: 0 10px;
}

.app {
  margin-left: 30px;
  margin-top: 0px;
}

.appMain {
  background-color: rgba(210, 235, 13, 0.31) ;
  min-height: 500px;
}

footer {
  background-color: #000;
  align-items: flex-start;
  display: flex;
  flex-flow: column;
  color: #fff;
  margin-top: 20;
  height: 280px;
  width: 100%;
  line-height: 1.625rem;
}

.footer-main {
  margin-left: 50px;
  width: auto;
}

footer .footer-main a{
  color: #fff;
}

footer .footer-main h2{
  text-align: left;
}

footer .links {
  width: 100%;
  height: 135px;
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
}

footer .links img {
  cursor: pointer;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 20px;
  height: 30%;
  width: auto;

}

footer .links ul {
  list-style: none;
  display: grid;
  justify-content: space-around;
  grid-template-columns:  150px 150px;
  grid-template-rows: auto;
  grid-row-gap: 10px;
  grid-auto-flow: row;
}

footer .links ul >*{
  cursor: pointer;
  display: inline-block;
  margin-left: 0px;
  margin-right: 50px;
  padding-bottom: 0px;
  border-bottom: 1px solid white;
}

.slides {

  position: relative;
  .slick-prev, .slick-next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    z-index: 1 !important;
  }
  .slick-prev {
    left: 5% !important;
  }
  .slick-next {
    right: 5% !important;
  }
}

.title {
  padding: 20px 0px 0px;
  font-family: "Josefin Sans","\6E38\30B4\30B7\30C3\30AF\4F53",Yu Gothic,YuGothic,"\30D2\30E9\30AE\30CE\89D2\30B4   Pro",Hiragino Kaku Gothic Pro,"\30E1\30A4\30EA\30AA",Meiryo,sans-serif;
}

.musicart {
  font-family: Futura,Trebuchet MS,Arial,sans-serif;
  font-style: italic;
}

.contact-form input,textarea{
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top : 10px;
  margin-bottom : 30px;
  padding : 18px;
  font-size : 18px;
  border : 1px solid #dee7ec;
}

.contact-form .submit{
  background-color : #dee7ec;
  color : #889eab;
  -webkit-transition: .3s ease;
  transition: .3s ease;

}

.contact-form .submit:hover{
  background-color : #b7d4e4;
  color: rgb(136, 86, 223);
  cursor: pointer;
}

.twitter {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 420px;
}

@media only screen and (max-width:1018px) and (min-width:850px) {
  footer {
    height: 200px;
  }

  footer .links ul {
    grid-template-columns: repeat(5, 150px);
  }
}

@media only screen and (min-width:1018px) {
  footer {
    height: 150px;
  }
  footer .links ul{
    display: flex;
  }
  footer .links ul >*{
    width: 150px;
  }

}

@media only screen and (min-width:732px) and (max-width:850px) {
  footer {
    height: 200px;
  }

  footer .links ul {
    grid-template-columns: repeat(4, 150px);
  }
}

@media only screen and (min-width:732px){
  .twitter{
    width: 700px;
  }
  .contact-form input, textarea{
    width : 400px;
    display: block;
    margin-left: 0;
  }
}

@media only screen and (min-width:460px) and (max-width:732px){
  footer {
    height: 240px;
  }

  footer .links ul {
    grid-template-columns: 150px 150px 150px;
  }

  .twitter {
    width: 400px;
  }
}

.article {
  margin: 10px 30px
}

.navbar {
  -webkit-transition: .3s ease;
  transition: .3s ease;
  font-family: "Josefin Sans","\6E38\30B4\30B7\30C3\30AF\4F53",Yu Gothic,YuGothic,"\30D2\30E9\30AE\30CE\89D2\30B4   Pro",Hiragino Kaku Gothic Pro,"\30E1\30A4\30EA\30AA",Meiryo,sans-serif;
}

.navbar.hide {
  top: -100px;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

@media only screen and (min-width:768px){
  .navbar.hide {
    top: 0px;
    -webkit-transition: none;
    transition: none;
  }
  .navbar {
    -webkit-transition: none;
    transition: none;
  }
}

@media only screen and (max-width:460px){
  .navbar.hide {
    top: -150px;
  }
}

.navbar-light .navbar-nav .nav-link {
    line-height: 1.625rem;
    color: rgba(0,0,0,.7) ;
    -webkit-transition: .3s ease !important;
    transition: .3s ease !important;
}

.navbar-light .navbar-nav .nav-link :hover{
    color: #000 ;
}

.pic-component {
  width: 100%;
  display: flex;
  flex-flow: column;
  text-align: center;
}

.pic-img {
  margin-top: auto;
  margin-bottom: auto;
  display: block;
  width: 100%;
  height: auto;
}

@media only screen and (min-width:768px){
  .pic-img {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}

.carousel-control-prev-icon, .carousel-control-next-icon {
  background-color: black !important;
  background-repeat: no-repeat;
  background-position: center;
  width: 50px !important;
  height: 50px !important;
  background-size: 50% 50% !important;
  border-radius: 50% !important;
}

